<template>
    <div>
        <ManagerHeader></ManagerHeader>
        <SearchBar></SearchBar>

        <div class="wrap table-wrap">
            <el-table
                    ref="multipleTable"
                    :data="tableData"
                    border
                    tooltip-effect="dark"
                    style="width: 100%"
                    @selection-change="handleSelectionChange">
                <el-table-column
                        fixed
                        type="selection"
                        width="55">
                </el-table-column>
                <el-table-column
                        fixed
                        prop="casename"
                        label="案例名称"
                        width="150">
                </el-table-column>
                <el-table-column
                        prop="awards"
                        label="提报奖项"
                        width="120">
                </el-table-column>
                <el-table-column
                        prop="compnay"
                        label="上传公司"
                        width="120">
                </el-table-column>
                <el-table-column
                        prop="user"
                        label="用户姓名"
                        width="120">
                </el-table-column>
                <el-table-column
                        prop="telephone"
                        label="手机号"
                        width="120">
                </el-table-column>
                <el-table-column
                        prop="updateTime"
                        label="提交时间"
                        width="120">
                </el-table-column>
                <el-table-column
                        prop="checkStatus"
                        label="审核状态"
                        width="100">
                </el-table-column>
                <el-table-column
                        prop="checkTime"
                        label="审核时间"
                        width="100">
                </el-table-column>
                <el-table-column
                        prop="checker"
                        label="审核人"
                        width="100">
                </el-table-column>
                <el-table-column
                        prop="score"
                        label="评分"
                        width="100">
                </el-table-column>
                <el-table-column
                        prop="operation"
                        fixed="right"
                        label="操作"
                        width="100">

                    <template scope="scope">
                        <el-button @click="handleClick" type="text" size="small">查看</el-button>
                        <el-button type="text" size="small">编辑</el-button>
                    </template>
                </el-table-column>
            </el-table>
        </div>


    </div>
</template>

<script type="text/ecmascript-6">
    import ManagerHeader from '../header/Header.vue'
    import SearchBar from './SearchBar'
    export default {
        components: {
            ManagerHeader,
            SearchBar
        },
        data(){
            return {
                tableData: [{
                    casename: 'xxxxxx案例',
                    awards: 'xxxx',
                    company: 'xxxx',
                    user: '老虎',
                    telephone: '11111111111',
                    updateTime: '1999-2-1',
                    checkStatus: '通过',
                    cehckTime: '1999-1-1',
                    checker:'Tom',
                    score:'59',

                }],
                multipleSelection: []
            }
        },
        methods: {
            toggleSelection(rows) {
                if (rows) {
                    rows.forEach(row => {
                        this.$refs.multipleTable.toggleRowSelection(row);
                    });
                } else {
                    this.$refs.multipleTable.clearSelection();
                }
            },
            handleSelectionChange(val) {
                this.multipleSelection = val;

            },
            handleClick() {
                console.log(val);
            }
        }
    }
</script>


<style lang="stylus" rel="stylesheet/stylus" scoped>
    .table-wrap
        margin-top: 40px

</style>



